const API_URL = 'http://localhost:8000/api/'

const compl = ['auth/']


// Usuario
export function login(userData) {
  return axios.post(`${API_URL}${compl[0]}login`, userData, {
    headers: headers
  })
}

// Usuario
export function infoUser(accessToken) {
  return axios.get(`${API_URL}${compl[0]}infoUser`, {
    headers: {
      Authorization: `Bearer ${accessToken}`
    }
  })
}

