  methods: {
    animation: () => {
      console.log("running...");
      anime({
        targets: "path",
        strokeDashoffset: [anime.setDashoffset, 0],
        easing: "easeInOutCubic",
        duration: 5000,
        delay: function (el, i) {
          return i * 250;
        },
        direction: "alternate",
        begin: function () {
          document.querySelector("path").setAttribute("stroke", "black");
          document.querySelector("path").setAttribute("fill", "none");
        },
        complete: function () {
          document.querySelector("path").setAttribute("fill", "#0455BF");
        },
        loop: false,
        autoplay: true
      });
