<template>
  <nav >
    <v-dialog v-model="loading" transition="dialog-bottom-transition" fullscreen class="loaderContainer">
      <div class="loader"></div>
      <div class="message">Cargando la magia...</div>
    </v-dialog>
    <nav v-if="getUserData.id != 0">
      <MenuUser />
      <router-view />
    </nav>
    <nav v-else>
      <LoginViewVue></LoginViewVue>
    </nav>
  </nav>
</template>

<script>
import axios from "axios";
import "animate.css";
import { mapGetters, mapActions } from "vuex";
import LoginViewVue from "./views/LoginView.vue";
import MenuUser from '@/components/MenuUser.vue';


export default {
  components: {
    LoginViewVue,
    MenuUser
  },
  data() {
    return {
      loading: false,
    };
  },

  computed: {
    ...mapGetters("auth", {
      getAccessToken: "getAccessToken",
      getUserData: "getUserData",
    }),
  },

  methods: {
    ...mapActions("auth", {
      validateLogin: "validateLogin",
    }),

    async validateLoginFun() {
      this.validateLogin()
    },
  },

  created() {
    this.validateLoginFun()
    axios.interceptors.request.use(
      (config) => {
        this.loading = true;
        if (this.getAccessToken != "" && this.getAccessToken != undefined) {
          config.headers["Authorization"] = `Bearer ${this.getAccessToken}`;
        }
        return config;
      },
      (error) => {
        this.loading = false;
        return Promise.reject(error);
      }
    );

    axios.interceptors.response.use(
      (response) => {
        this.loading = false;

        return response;
      },
      (error) => {
        this.loading = false;
        return Promise.reject(error);
      }
    );
  },
};
</script>

