<template>
  <v-card class="mx-auto" prepend-icon="mdi-home" width="400">
    <div>
      <img alt="Vue logo" class="logoImg" src="../assets/logo.svg" />
    </div>

    <v-card-text> Inicio de sesión a Portal PPSColombia </v-card-text>
    <v-form validate-on="submit lazy" @submit.prevent="submit">
      <v-text-field
        v-model="userName"
        label="Correo Electrónico"
      ></v-text-field>

      <v-text-field v-model="password" label="Contraseña" type="password"></v-text-field>

      <v-btn
        :loading="loading"
        class="mt-2"
        text="Submit"
        type="submit"
        block
      ></v-btn>
    </v-form>
  </v-card>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
// @ is an alias to /src

export default {
  name: "HomeView",
  components: {},

  data() {
    return {
      userName: "",
      password: "",
      loading: false,
    };
  },

  computed: {
    ...mapGetters("auth", {
      getLoginApiStatus: "getLoginApiStatus",
    }),
  },

  methods: {
    ...mapActions("auth", {
      actionLoginApi: "loginApi",
    }),

    async submit() {
      const payload = {
        email: this.userName,
        password: this.password,
      };
      this.actionLoginApi(payload).then(() => {
        if (this.getLoginApiStatus == "success") {
          //this.$router.push("/dashboard");
        } else {
          alert("failed");
        }
      });
    },
  },
};
</script>

<style>
.logoImg {
  width: 100%;
}
v-card {
  text-align: center;
}
</style>
