import { login, infoUser } from "@/api";
import Cookies from 'js-cookie'

const state = () => ({
  loginApiStatus: "failed",
  accessToken: "",
  refreshToken: "",
  userData: {
    id: 0,
    email: "",
    nit: "",
    name: "",
  },
});

const getters = {
  getLoginApiStatus(state) {
    return state.loginApiStatus;
  },
  getAccessToken(state) {
    return state.accessToken;
  },
  getRefreshToken(state) {
    return state.refreshToken;
  },
  getUserData(state) {
    return state.userData;
  },
};

const actions = {
  async validateLogin({ commit, dispatch }) {
    console.log("AccessToken", Cookies.get("accessToken"))
    if (Cookies.get("accessToken") && Cookies.get("refreshToken") && Cookies.get("userData")) {
      commit("setLoginApiStatus", "success");
      commit("setAccessToken", Cookies.get("accessToken"));
      commit("setRefreshToken", Cookies.get("refreshToken"));
      commit("setUserData", Cookies.get("userData"));
      dispatch("userDataApi", Cookies.get("accessToken"))
    } 
  },

  async loginApi({ commit }, payload) {
    const response = await login(payload).catch((err) => {
      console.log(err);
    });

    if (response && response.data) {
      commit("setLoginApiStatus", "success");
      commit("setAccessToken", response.data.accessToken);
      commit("setRefreshToken", response.data.refreshToken);

      const responseUserData = await infoUser(response.data.accessToken).catch(
        (err) => {
          console.log(err);
        }
      );

      if (responseUserData && responseUserData.data) {
        //commit("setLoginApiStatus", "success");
        commit("setUserData", {
          id: responseUserData.data.user.id,
          email: responseUserData.data.user.email,
          nit: responseUserData.data.user.nit,
          name: responseUserData.data.user.name,
        });
      } else {
        //commit("setLoginApiStatus", "failed");
        commit("setUserData", {
          id: 0,
          email: "",
          nit: "",
          name: "",
        });
        if (state.userData.id == 0) {
          return Promise.reject("User not valid");
        }
      }
    } else {
      commit("setLoginApiStatus", "failed");
      commit("setAccessToken", "");
      commit("setRefreshToken", "");
    }
  },
  async userDataApi({ commit }, accessTokenTemp) {
    const response = await infoUser(accessTokenTemp).catch(() => {
      commit("setUserData", {
        id: 0,
        email: "",
        nit: "",
        name: "",
      });
      commit("setLoginApiStatus", "failed");
      commit("setAccessToken", "");
      commit("setRefreshToken", "");
    });


    if (response && response.data) {
      //commit("setLoginApiStatus", "success");
      commit("setUserData", {
        id: response.data.user.id,
        email: response.data.user.email,
        nit: response.data.user.nit,
        name: response.data.user.name,
      });
    }
  },

  async closeSession({ commit }) {
    commit("setUserData", {
      id: 0,
      email: "",
      nit: "",
      name: "",
    });
    commit("setLoginApiStatus", "failed");
    commit("setAccessToken", "");
    commit("setRefreshToken", "");
  },
};

const mutations = {
  setLoginApiStatus(state, data) {
    state.loginApiStatus = data;
    Cookies.set("loginApiStatus", data, { expires: 3, secure: true, SameSite: "Strict" })
    

  },
  setAccessToken(state, data) {
    state.accessToken = data;
    Cookies.set("accessToken", data, { expires: 3, secure: true, SameSite: "Strict" })
    
  },
  setRefreshToken(state, data) {
    state.refreshToken = data;
    Cookies.set("refreshToken", data, { expires: 3, secure: true, SameSite: "Strict" })
  },
  setUserData(state, data) {
    state.userData = data;
    Cookies.set("userData", data, { expires: 3, secure: true, SameSite: "Strict" })
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
